* {
  box-sizing: border-box;
}

p {
  margin: 0;
}

body {
  margin: 0;
  padding: 100px 0 20px;
}

.is-hidden {
  display: none;
  visibility: hidden;
  pointer-events: none;
}

.search-block {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 16px 0;
  background-color: rgb(45, 34, 211);
  box-shadow: 0px 10px 8px -5px rgba(112, 125, 127, 0.8);
  z-index: 10;
}

.search-form {
  display: flex;
  height: 40px;
  width: max-content;
  margin: 0 auto;
  border: 1px solid rgb(67, 67, 67);
  border-radius: 4px;
  overflow: hidden;
}

.search-form input {
  width: 270px;
  padding: 5px 10px;
  border: 0;
  outline: 0;
  font-size: 16px;
}

.search-form button {
  width: 40px;
  padding: 0;
  border: 0;
  outline: 0;
  cursor: pointer;
}

.search-icon {
  width: 30px;
  height: 30px;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  max-width: 1200px;
  margin: 0 auto;
}

.photo-card {
  max-width: 380px;
  margin-bottom: 20px;
  border-radius: 4px;
  box-shadow: 0px 3px 4px 0px #b5b5b5;
  overflow: hidden;
  transition: opacity 150ms;
  &:nth-last-child(-n + 3) {
    margin-bottom: 0;
  }
}

.img-wrapper{
    position: relative;
    overflow: hidden;
}

.gallery__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__link {
  display: block;
  text-decoration: none;
  height: 80%;
  transition: transform, 250ms, linear;
}

.info {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 20%;
}

.info-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.load-more {
  margin-left: auto;
}

@keyframes spinner {
    100% {
        transform: rotate(3600deg);
    }
}

.loading-container {
    top: 30px;
    margin: 0 auto;
    text-align: center;
    position: relative;
    transform: translateY(-50%);
}

.spinner {
    display: inline-block;
    width: 50px;
    height: 50px;
    background: url('http://i.imgur.com/oSHLAzp.png') center center;
    background-size: contain;
    transition: 50% 50%;
    animation: spinner 6s infinite alternate ease-in-out;
}

.spinner-center {
    display: inline-block;
    position: absolute;
    margin-left: -50px;
    width: 50px;
    height: 50px;
    background: url('http://i.imgur.com/u0BC2ZR.png') center center;
    background-size: contain;
    content: '';
}

.loading-text {
    position: relative;
    z-index: 1;
    font-size: 1.5rem;
    font-family: "Comic Sans MS", cursive, sans-serif;
    margin-left: 0.5em;
}